.home-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.btn {
  display: inline-block;
  padding: 12px 24px;
  background-color: #502bc0;
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.btn:hover {
  background-color: #502bc0;
}

/* Sobre.css */
.sobre-container {
  background-color: #222;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Definindo a altura para cobrir a tela inteira */
  margin-top: 20px; /* Espaçamento superior */
  border: 1px solid #502bc0; /* Cor da borda */
  border-radius: 10px; /* Raio da borda */
}

.card-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  background-color: #502bc0;
  border-radius: 10px;
  padding: 20px;
  margin: 10px;
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 20px;
}

.card-content {
  color: #fff;
  text-align: center;
}

.card h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.card p {
  font-size: 16px;
}

/* Menu.css */
.menu {
  background-color: #191919;
  padding: 10px 0;
}

.menu ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.menu li {
  margin: 0 10px;
}

.menu li a {
  color: #fff;
  text-decoration: none;
  padding: 5px 10px;
  font-weight: 600;
  transition: color 0.3s ease;
  position: relative;
}

.menu li a:before {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;  
  width: 100%;
  height: 2px;
  background-color: #502bc0;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.menu li a:hover:before {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.menu li a:after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #502bc0;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  -webkit-transform-origin: right;
          transform-origin: right;
}

.menu li a:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: left;
          transform-origin: left;
}

/* Menu.css */
.menu li.active a:before {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.menu li.active a:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.menu-container {
  margin-bottom: 20px; /* Espaçamento inferior */
  border: 1px solid #502bc0; /* Cor da borda */
  border-radius: 10px; /* Raio da borda */
  background-color: #191919; /* Cor de fundo */
}

.has-dropdown .dropdown {
  display: none;
}

.has-dropdown.active .dropdown {
  display: block;
}

.input-card {
    background-color: #f0f0f0;
    padding: 20px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
    -webkit-transform-origin: center;
            transform-origin: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    }

.input-card:hover {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
}

.input-row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    margin-bottom: 10px;
}

.input-group {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.input-group label {
    margin-right: 10px;
}

.input-group p {
    margin-top: 5px;
}

.button-container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.canvas-container {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid black;
    width: 500px;
    height: 500px;
    margin: 0 auto;
  }
